<template>
    <div class="addr-modify">
        <van-nav-bar
            :title="`${title}`"
            right-text=""
            left-arrow
            @click-left="back"
            @click-right="save"
        ></van-nav-bar>
        <van-popup v-model="show" position="top" :overlay="false" v-text="message"></van-popup>
        <van-cell-group>
            <van-cell>
                <template slot="title">
                    <input class="left-prompt" ref="contacts" type="text" v-model="contacts" placeholder=""/>
                </template>
                <div slot="right-icon">
                    <span class="iconfont icon-contacts">&#xe6e7;</span>
                </div>
            </van-cell>
            <van-cell>
                <template slot="title">
                    <input class="left-prompt" ref="telephone" type="tel" v-model="telephone" placeholder="" maxlength="12"/>
                </template>
                <div slot="right-icon">
                    <span class="telephone">+86</span>
                </div>
            </van-cell>
            <van-cell  @click="CityListShow(true)">
                <template slot="title">
                    <input class="left-prompt" ref="region" type="text" readonly :placeholder="region"/>
                </template>
                <div slot="right-icon">
                    <span class="telephone"><span class="iconfont icon-tel">&#xe667;</span></span>
                </div>
            </van-cell>
            <van-cell>
                <template slot="title">
                    <textarea class="left-prompt left-area" ref="address" type="text" v-model="address" placeholder=""/>
                </template>
            </van-cell>
            <div class="addr-wrap">
                <van-cell title="" class="set-default">
                    <template slot="right-icon">
                        <van-switch v-model="checked" />
                    </template>
                </van-cell>
                <van-cell v-if="delFlag">
                    <template slot="title">
                        <button class="left-prompt left-btn" @click="del"></button>
                    </template>
                </van-cell>
            </div>
        </van-cell-group>
        <van-popup v-model="cityShow" position="left" :overlay="false">
            <van-area :area-list="areaList" :columns-num="3" :visible-item-count="3" @confirm="setCity($event)" @cancel="CityListShow(false)" />
        </van-popup>
    </div>    
</template>

<script>
import { request } from '@/assets/javascript/request.js'
import { mapState,mapMutations } from 'vuex'
export default {
    name: 'AddrModify',
    data() {
        return {
            show: false,
            message: '',
            contacts: '',
            telephone: '',
            region: '',
            address: '',
            checked: false,
            index: 1,
            areaList : {},
            cityShow: false
        }
    },
    computed: {
        ...mapState(['addressArr','addrIndex']),
        type() {
            return this.$route.params.type;
        },
        title() {
            return this.type == 'edit' ? '' : '';
        },
        delFlag() {
            if(this.type == 'edit') {
                this.setVal();
                return true;
            }else {
                this.setDefault();
                return false;
            }
        }
    },
    methods: {
        ...mapMutations(['addAddr','editAddr','delAddr']),
        init() {
            this.$axios.get(request.url.city).then(this.getData)
        },
        getData(res) {
            if(res.status == 200 && res.data.data) {
                this.areaList = res.data.data;
            }
        },
        back() {
            this.$router.replace('/address_manage');
        },
        save() {
            if(this.isContacts() && this.isTelephone() && this.isRegion() && this.isAddress()) {
                let targetObj = {
                    contacts: this.contacts,
                    telephone: this.telephone,
                    region: this.region,
                    address: this.address,
                    checked: this.checked
                }
                if(this.delFlag) {
                    targetObj.id = this.addrIndex;
                    this.editAddr(targetObj);
                }else {
                    targetObj.id = this.index++;
                    this.addAddr(targetObj);
                }
                this.back();
            }
        },
        setVal() {
            let arr = this.addressArr.filter((item) => item.id == this.addrIndex);
            this.setDefault(arr[0]);
        },
        setDefault(obj = {}) {  //
            this.contacts = obj.contacts || '';
            this.telephone = obj.telephone || '';
            this.region = obj.region || '';
            this.address = obj.address || '';
            this.checked = obj.checked || false;
        },
        del() {
            this.delAddr(this.addrIndex);
            this.back();
        },
        setCity(e) {    //
            // Areaevent
            //[ {code: '',name: ''},{code: '',name: ''},{code: '',name: ''} ]
            let [{name: province},{name: city},{name: county}] = e;
            this.region = `${province} ${city} ${county}`;
            this.CityListShow(false);
        },
        CityListShow(flag) {
            this.cityShow = flag;
        },
        popup(el,info) {    //
            el.focus();
            this.message = info;
            this.show = true;
            setTimeout(() => {
                this.show = false;
            },2000);
        },
        isContacts() {      // 
            let reg = /^([\u4e00-\u9fa5]{1,}|[a-zA-Z\.\s]{1,})$/;
            if(reg.test(this.contacts)) {
                return true;
            }
            this.contacts = '';
            this.popup(this.$refs.contacts,'');
        },
        isTelephone() {     //
            let reg = /^(1[3456789]\d{9})$|^(0\d{2,3}-\d{7,8})$/;
            if(reg.test(this.telephone)) {
                return true;
            }
            this.telephone = '';
            this.popup(this.$refs.telephone,'');
        },
        isRegion() {    //
            if(this.region == '') {
                this.popup(this.$refs.region,'');
                return false;
            }
            return true;
        },
        isAddress() {   //
            if(this.address != '') {
                return true;
            }
            this.popup(this.$refs.address,'');
        }
    },
    created() {
        this.init();
    }
}
</script>

<style lang="stylus" scoped>
    @import '~stylus/mixin'
    .addr-modify
        max_width() 
        full_screen(2,fixed,0,0,0,0,$lightGray)
        van_nav(#FF9900)
        van_popup()
        .addr-wrap
            margin-top .2rem
            .set-default
                font-size .3rem
        & >>> .van-cell-group 
            background-color transparent
            .van-cell
                text-align left
                font-weight normal
                position relative
                &:not(:last-child)::after
                    left 0
                .left-prompt
                    placeholder(.32rem)
                    width 90%
                    height 100%
                    &.left-area
                    &.left-btn
                        border_none()
                        resize none
                    &.left-btn
                        color $red
                        width 100%
                        text-align left
                        padding 0
                        background-color transparent
                .icon-contacts
                    font-weight bold
                    font-size .52rem
                .telephone
                    color $darkGray
                    font-size .3rem
                    margin-right -.1rem
                    .icon-tel
                        font-size .28rem
        & >>> .van-switch
            font-size .4rem!important
        & >>> .van-popup--left
            width 100%
            height 100%
            background-color rgba(0,0,0,.3)
            .van-picker
                position fixed
                bottom 0
                width 100%
</style>



